



































import Vue from "vue";
import * as echarts from "echarts";

export default Vue.extend({
  name: "DetectionDashboard",

  data: () => ({}),
  mounted: function () {
    // 基于准备好的dom，初始化echarts实例
    var myChart = echarts.init(this.getChartElement());
    var option = {
      grid: {
        top: "3%",
        bottom: "8%",
        right: "5%",
      },
      xAxis: {
        type: "category",
        boundaryGap: false,
      },
      yAxis: {
        type: "value",
        boundaryGap: [0, "30%"],
      },
      visualMap: {
        type: "piecewise",
        show: false,
        dimension: 0,
        seriesIndex: 0,
        pieces: [
          {
            gt: 1,
            lt: 3,
            color: "rgba(0, 0, 180, 0.4)",
          },
          {
            gt: 5,
            lt: 7,
            color: "rgba(0, 0, 180, 0.4)",
          },
        ],
      },
      series: [
        {
          type: "line",
          smooth: 0.6,
          symbol: "none",
          lineStyle: {
            color: "#5470C6",
            width: 5,
          },
          markLine: {
            symbol: ["none", "none"],
            label: { show: false },
            data: [{ xAxis: 1 }, { xAxis: 3 }, { xAxis: 5 }, { xAxis: 7 }],
          },
          areaStyle: {},
          data: [
            ["10", 200],
            ["11", 560],
            ["12", 750],
            ["13", 580],
            ["14", 250],
            ["15", 300],
            ["16", 450],
            ["17", 300],
            ["18", 100],
          ],
        },
      ],
    };
    myChart.setOption(option);
  },
  methods: {
    getChartElement(): HTMLElement {
      return document.getElementById("dashboard-line-chart-el") as HTMLElement;
    },
    chartOnResize() {
      this.$nextTick(() => {
        echarts.getInstanceByDom(this.getChartElement())?.resize();
        setTimeout(() => {
          echarts.getInstanceByDom(this.getChartElement())?.resize();
        }, 300);
        setTimeout(() => {
          echarts.getInstanceByDom(this.getChartElement())?.resize();
        }, 1000);
        setTimeout(() => {
          echarts.getInstanceByDom(this.getChartElement())?.resize();
        }, 2000);
        setTimeout(() => {
          echarts.getInstanceByDom(this.getChartElement())?.resize();
        }, 5000);
      });
    },
    disposeChart() {
      echarts.getInstanceByDom(this.getChartElement())?.dispose();
    },
  },
  destroyed: function () {
    console.log("dispose..");
    // this.disposeChart();
  },
});
