  computed: {
    tableHeight(): number {
      return this.$vuetify.breakpoint.height - this.getRemPx() * 4.875 - 60;
    },
    itemsPerPage(): number {
      const tableHeight = this.tableHeight;
      const remPx = this.getRemPx();
      const thHeight = remPx * 3.5;
      const tdHeight = remPx * 3;
      const itemsPerPage = Math.round((tableHeight - thHeight) / tdHeight);
      return Math.max(itemsPerPage, 1);
    },
    footerProps() {
      const options = [5, 10, 15, 20, 25, 30, 50, 100];
      const itemsPerPage = this.itemsPerPage;
      const itemsPerPageOptions: number[] = [
        itemsPerPage,
        ...options.filter((e) => e > itemsPerPage),
        -1,
      ];
      return { "items-per-page-options": itemsPerPageOptions };
    },
  },
  methods: {
    getRemPx(): number {
      const remPx = getComputedStyle(document.documentElement).fontSize;
      return parseFloat(remPx.slice(0, -2));
    },
    save() {
      this.snack = true;
      this.snackColor = "success";
      this.snackText = "Data saved";
    },
    cancel() {
      this.snack = true;
      this.snackColor = "error";
      this.snackText = "Canceled";
    },
    open() {
